<template>
    <div>
        <v-row justify="space-around">
            <v-col cols="12">
                <v-sheet tile height="64" class="d-flex">
                    <v-toolbar flat>
                        <v-btn outlined class="mr-4" color="grey darken-2" @click="setToday">
                            Today
                        </v-btn>
                        <v-btn fab text small color="grey darken-2" @click="$refs.calendar.prev()">
                            <v-icon small>
                                mdi-chevron-left
                            </v-icon>
                        </v-btn>
                        <v-btn fab text small color="grey darken-2" @click="$refs.calendar.next()">
                            <v-icon small>
                                mdi-chevron-right
                            </v-icon>
                        </v-btn>
                        <v-toolbar-title>
                            {{ calendarTitle }}
                        </v-toolbar-title>
                        <v-spacer></v-spacer>
                    </v-toolbar>
                </v-sheet>
                <v-sheet height="600">
                    <v-calendar ref="calendar" v-model="calVal" :weekdays="[0, 1, 2, 3, 4, 5, 6]" color="#ed0025"
                        locale="zh" type="month" event-overlap-mode="stack" :event-overlap-threshold="30"
                        :events="events">
                    </v-calendar>
                </v-sheet>
            </v-col>
        </v-row>
    </div>
</template>
<script>
export default {
    data() {
        return {
            calVal: '',
            events: [{
                name: '1',
                start: '2022-11-10',
                end: '2022-11-10',
                timed: true,
                color:''
            }, {
                name: '3',
                start: '2022-11-11',
                end: '2022-11-24',
                timed: false,
            }],
            calendarTitle: ''
        }
    },
    mounted() {
        this.calendarTitle = this.$refs.calendar.title
        this.$refs.calendar.checkChange()
    },
    methods: {
        setToday() {
            this.focus = ''
        },
    }
}
</script>